.contact {
    background-color: #f5f5f5;
    padding: calc(20px + 80 * ((100vw - 320px) / 1046));
}

.info {
    display: flex;
    flex-direction: row;
}

.info .text__container {
    flex-basis: auto;
    padding-right: 10%;
}

.form {
    flex-shrink: 0;
    flex-basis: calc(672/1166*100%);
    min-width: 415px;
}

.contact .title {
    margin-bottom: calc(35/671*100%);
}

.contact p {
    font-family: 'Nunito Sans', sans-serif;
    color: #787878;
    line-height: normal;
}

.contact_way {
    font-weight: bold;
    text-transform: capitalize;
    color: black;
}

.user__information {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: calc(30/671*100%);
}

.user__name {
    flex-basis: calc(320/671*100%);
    margin-bottom: calc(30/671*100%);
}

.user__email {
    margin-bottom: calc(30/671*100%);
    flex-basis: calc(320/671*100%);
}

.user__title {
    display: block;
    width: 100%;
    margin-bottom: calc(30/671*100%);
}

.user__comment {
    display: block;
    width: 100%;
}

input:focus, textarea:focus {
    outline: none;
}

input, textarea {
    border: none;
    border-radius: 2px;
    padding: 15px;
    resize: none;
    color: #787878;
    font-size: 15px;
    font-family: 'Nunito Sans', sans-serif;
    letter-spacing: 0.5px;
}

:-moz-placeholder,
:-ms-input-placeholder,
::placeholder,
::-webkit-input-placeholder {
    color: #787878;
    font-size: 15px;
    font-family: 'Nunito Sans', sans-serif;
}

@media all and (max-width: 937px) {
    .info {
        display: block;
    }

    .form {
        min-width: auto;
    }
}

@media all and (max-width: 767px) {
    .contact {
        padding-left: 5%;
        padding-right: 5%;
    }
}

@media all and (max-width: 599px) {
    .user__name, .user__email {
        display: block;
        width: 100%;
        flex-basis: auto;
    }
}