html,
body {
    margin: 0;
    padding: 0;
}

* {
    box-sizing: border-box;
    line-height: calc(9/12);
}

header {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding: calc(38/1366*100%) calc(98/1366*100%);
    width: 100%;
    z-index: 50;
}

.burger {
    display: none;
    position: relative;
    z-index: 50;
}

.logo {
    position: relative;
    z-index: 50;
}

.nav__list {
    list-style-type: none;
    display: inline-block;
    padding-left: 0;
    width: 100%;
    height: 100%;
    margin: 0;
    text-align: right;
}

.nav__item {
    color: #191919;
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 1%;
    height: 100%;
}

.nav__link {
    text-decoration: none;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
    font-size: 12px;
    color: inherit;
    font-weight: 600;
    display: inline-block;
    transition: all 0.2s;
}

.nav__item:hover {
    color: #10c9c3;
}

.nav {
    margin-left: auto;
    flex-grow: 1;
}

.nav__bar {
    height: 100%;
}

.title {
    text-transform: capitalize;
    font-family: 'Montserrat', sans-serif;
    font-size: calc(22px + 8 * ((100vw - 320px) / 1046));
    color: #191919;
    margin-top: 0;
    margin-bottom: calc(35/618*100%);
}

.text {
    color: #787878;
    font-family: 'Nunito Sans', sans-serif;
    font-size: calc(14px + 2 * ((100vw - 320px) / 1046));
    line-height: 1.5;
    margin-top: 0;
}

footer {
    display: flex;
    background-color: #141414;
    padding: calc(30px + 70 * ((100vw - 768px) / 598));
    padding-right: calc(30px + 80 * ((100vw - 768px) / 598));
    flex-direction: row;
    flex-wrap: wrap;
}

.emblem p {
    margin: 0;
}

.emblem p:last-child {
    margin-top: 10px;
}

.emblem__title {
    color: white;
    text-transform: capitalize;
    font-size: 18px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    margin-bottom: calc(30/222*100%);
}

.emblem__data {
    font-size: 15px;
    font-family: 'Nunito Sans', sans-serif;
    color: #bcbcbc;
}

.contact__info {
    color: #bcbcbc;
    font-family: 'Nunito Sans', sans-serif;
    font-size: 15px;
    margin-left: calc(50px + 190 * ((100vw - 768px) / 598));
}

.contact__email {
    margin-top: 0;
    margin-bottom: 10px;
}

.contact__phone {
    margin: 0;
}

.info__container {
    color: #bcbcbc;
    font-family: 'Nunito Sans', sans-serif;
    font-size: 15px;
    text-transform: capitalize;
    margin-left: calc(50px + 90 * ((100vw - 768px) / 598));
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.contact__button {
    display: inline-block;
    text-decoration: none;
    text-transform: uppercase;
    color: white;
    font-size: calc(8px + 4 * ((100vw - 320px) / 1046));
    font-family: 'Montserrat', sans-serif;
    background-color: #10c9c3;
    padding: calc(10.5px + 12 * ((100vw - 320px) / 1046)) calc(23.65px + 27.015 * ((100vw - 320px) / 1046));
    border: none;
}

.info__link {
    text-decoration: none;
    color: inherit;
    display: block;
}

.useful-links {
    color: #bcbcbc;
    font-family: 'Nunito Sans', sans-serif;
    font-size: 15px;
    text-transform: capitalize;
    margin-left: calc(50/1106*100%);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.useful-link {
    text-decoration: none;
    color: inherit;
    display: block;
}

.social-networks {
    color: #bcbcbc;
    font-family: 'Nunito Sans', sans-serif;
    font-size: 15px;
    text-transform: capitalize;
    margin-left: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.social-network {
    text-decoration: none;
    color: inherit;
    display: block;
}

@media all and (max-width: 767px) {
    header {
        align-items: center;
    }

  .nav {
        position: absolute;
        z-index: 2;
        top: 0;
        right: 0;
        transition: all 0.5s ease-in;
        transform: scale(0);
        transform-origin: top right;
        width: 100%;
        padding: 5%;
        display: block;
        background: linear-gradient(45deg, rgba(158,157,155,1) 0%, rgba(252,134,96,1) 100%);
    }

    .nav__list {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        text-align: left;
    }

    .nav.visible {
        transform: scale(1);
    }
    
    .burger {
        display: block;
        position: relative;
        height: 20px;
        width: 30px;
        margin-left: auto;
        transition: all 0.5s ease-in-out;
    }

    .burger::before,
    .burger::after {
        content: '';
        position: absolute;
        background-color: #191919;
        width: 30px;
        height: 2px;
        transition: all 0.5s ease-in-out;
    }
    
    .burger::before {
        top: 0;
    }
    
    .burger::after {
        bottom: 0;
    }
    
    .burger span {
        background-color: #191919;
        position: absolute;
        width: 30px;
        display: block;
        top: 50%;
        margin-top: -1px;
        transition: all 0.5s ease-in-out;
        height: 2px;
    }

    .burger.active::before {
        transform: rotate(405deg);
        transform-origin: bottom left;
        /* top: 9px; */
        background-color: #dd4b2b;
    }
    
    .burger.active::after {
        /* bottom: 9px; */
        transform: rotate(315deg);
        background-color: #dd4b2b;
        transform-origin: top left;
    }
    
    .burger.active span {
        transform: scale(0);
        background-color: #dd4b2b;
    }

    br {
        display: none;
    }
}

@media all and (max-width: 767px) {
    footer {
        padding: 5%;
    }
}

@media all and (max-width: 663px) {
    .contact__info {
        flex-basis: 50%;
        margin-left: 0;
        padding-left: 10%;
    }

    .emblem {
        margin-bottom: 5%;
        flex-basis: 50%;
    }

    .social-networks, .useful-links, .info__container {
        margin: auto;
    }
}

@media all and (max-width: 585px) {
  header {
        padding-top: 5%;
        padding-bottom: 5%;
    }
}

@media all and (max-width: 499px) {
    footer {
        display: block;
    }

    .emblem__title {
        margin-bottom: calc(30/449*100%);
    }

    .contact__info {
        padding-left: 0;
    }
    
    footer div {
        margin-bottom: 10%;
    }

    .social-networks, .useful-links, .info__container {
        width: calc(288/3/320*100%);
        display: inline-block;
    }

    footer div a {
        margin-bottom: 10%;
    }
}