.about {
    background-color: #f5f5f5;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.about__title {
    text-transform: none;
    margin-bottom: calc(35/458*100%);
}

.photo {
    width: 50%;
    min-width: 300px;
}

.about .text__container {
    flex-basis: 50%;
    padding-left: calc(25px + 55 * ((100vw - 874px) / 491));
    padding-right: calc(25px + 120 * ((100vw - 874px) / 491));
}

.about__text {
    margin-bottom: calc(30/458*100%);
}

.banner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    padding: 0 calc(10px + 407 * ((100vw - 320px) / 1046));
    padding-bottom: 0;
    z-index: 1;
    height: calc(200px + 150 * ((100vw - 320px) / 1046));
    text-align: center;
}

.banner__image {
    width: 100%;
    position: absolute;
    top: 0;
    height: 100%;
    left: 0;
    z-index: -2;
    object-fit: cover;
}

.banner__title {
    font-size: calc(22px + 16 * ((100vw - 320px) / 1046));
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    color: white;
    margin-top: 0;
    text-transform: capitalize;
}

.banner__description {
    font-size: calc(12px + 6 * ((100vw - 320px) / 1046));
    line-height: 1.5;
    font-family: 'Nunito Sans', sans-serif;
    color: white;
    margin: 0;
}

.statistic {
    background-color: #10c9c3;
    color: white;
    font-family: 'Montserrat', sans-serif;
    display: flex;
    padding: calc(100/1366*100%);
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

.statistic__elem {
    flex-basis: 25%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    min-width: 200px;
}

.number {
    margin-top: 0;
    margin-bottom: calc(15/150*100%);
    font-size: 18px;
}

.name {
    margin: 0;
    font-size: 12px;
    text-transform: uppercase;
}

.statistic__elem .text__container {
    margin-left: calc(25/291.5*100%);
}

.project_order {
    text-align: center;
    padding: calc(50px + 55 * ((100vw - 320px) / 1046)) calc(5px + 407 * ((100vw - 320px) / 1046)) calc(40px + 60 * ((100vw - 320px) / 1046));
}

.project_order .title {
    text-transform: none;
}

@media all and (max-width: 937px) {
    .statistic__elem {
        margin: 5%;
    }
}

@media all and (max-width: 803px) {
    .about {
        display: block;
        text-align: center;
    }
    
    .about .text__container {
        padding: 5% 10%;
    }

    .photo {
        width: 100%;
    }

    .about__title {
        margin-bottom: calc(16px + 19 * ((100vw - 320px) / 483));
    }

    .about__text {
        margin-bottom: calc(16px + 14 * ((100vw - 320px) / 483));
    }
}

@media all and (max-width: 585px) {
    .statistic__elem {
        margin: 7%;
    }
}

@media all and (max-width: 399px) {
    .banner__title {
        margin-bottom: 3%;
    }

    .banner__description {
        line-height: normal;
    }
}