.home .text__container {
    background-color: #f6f6f6;
    text-align: center;
    padding: calc(20px + 80 * ((100vw - 320px) / 1046)) calc(10px + 407 * ((100vw - 320px) / 1046));
}

.home .title {
    font-size: calc(28px + 10 * ((100vw - 320px) / 1046));
    margin-bottom: calc(30/532*100%);
}

.home .text {
    font-size: calc(15.75px + 2.25 * ((100vw - 320px) / 1046));
    margin: 0;
}

.works__container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.work {
    flex-basis: 25%;
}

.work img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@media all and (max-width: 767px) {
    .work {
        flex-basis: 50%;
        min-width: 250px;
    }
}

@media all and (max-width: 499px) {
    .work {
        flex-basis: 100%;
    }
}